import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/views/home.vue')
    },
    {
      path: '/manage',
      name: 'manage',
      component: () => import('@/views/manage.vue')
    }
  ]
})



